import { DataServiceService } from './../../../services/data-service.service';

import { Component, OnInit } from '@angular/core';
import {MatDialog} from '@angular/material/dialog';
import { AuthCompComponent } from 'src/app/auth-comp/auth-comp.component';
import {ViewEncapsulation} from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { NgxUiLoaderService } from 'ngx-ui-loader'; // Import NgxUiLoaderService

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss'],
  encapsulation: ViewEncapsulation.None 

})
export class HeaderComponent implements OnInit {

  isLoggedin:boolean;
  userName:string;
  cartCount:number ;
  
  constructor(public dialog: MatDialog,
    private toastr: ToastrService,
    private ngxService: NgxUiLoaderService,
    private dataService: DataServiceService) {
      this.cartCount = this.dataService.cartCount;
     }

  ngOnInit() {
    const myData = JSON.parse(localStorage.getItem('user'));
    if(myData){
      this.userName = myData.login.userProfile.firstName;
      console.log('from local storage i got user', myData);
     
  
      this.isLoggedin = JSON.parse(localStorage.getItem('isLoggedIn'));
      // this.isLoggedin =  this.dataService.isLoggedin ;

      
      console.log('from local storage is login', this.isLoggedin );
    }
    


  }
  logmeIn(){
    console.log('log in');
    const dialogRef = this.dialog.open(AuthCompComponent,{
      width: '600px',
      height:'400px',
      data: {}
      
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log(`Dialog result: ${result}`);

      const myData = JSON.parse(localStorage.getItem('user'));
      console.log('from local storage i got user', myData);

      const isLog = JSON.parse(localStorage.getItem('isLoggedIn'));
      console.log('from local storage is login', isLog);
    });
  
  }

  logOut(){
    this.ngxService.start();
    console.log('log out clicked');
    localStorage.removeItem('user');
    this.isLoggedin = false;
    this.ngxService.stop();


  }
}
