import { DataServiceService } from './../../services/data-service.service';
import { Component, OnInit, Output, EventEmitter,Inject} from '@angular/core';
import { Apollo, QueryRef } from 'apollo-angular';
import gql from 'graphql-tag';
import { StoreService } from 'src/app/services/store-service.service';
import { getMaxListeners } from 'cluster';
import { NgxUiLoaderService } from 'ngx-ui-loader';
import { ToastrService } from 'ngx-toastr';
import { HeaderComponent } from '../../dashboard/home/header/header.component';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';

var localDbStorage = require('localStorage')
export interface DialogData {
  animal: string;
  name: string;
}
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})


export class LoginComponent implements OnInit {
  userObj = {
    email:'',
    password:''
  }

  user:any;
  //emit user status to parent
  @Output() userStatus: EventEmitter<any> = new EventEmitter<any>();

  constructor(private dataService : DataServiceService,
    private apollo:Apollo,
    private storage:StoreService,
    private ngxService: NgxUiLoaderService,
    private toastr: ToastrService,
    public dialogRef: MatDialogRef<HeaderComponent>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData
  ) { }

  ngOnInit() {
  
  }


  newUser(){
    console.log('new user called');
    this.dataService.isnewUser = true;
    this.userStatus.emit(this.dataService.isnewUser);
    console.log('is new user',this.dataService.isnewUser)
  }

  async login(){
    this.ngxService.start();
    try{
      const result:any = await this.doLogin();
      localDbStorage.setItem('user',JSON.stringify(result));
      localDbStorage.setItem("isLoggedIn","true");
      this.dataService.isLoggedin = true;
      console.log(result);
      this.user = result;
      this.ngxService.stop();
      this.onNoClick();
      //reload this page
      //this is temp fix
      location.reload();


    
    }catch(err){
      this.ngxService.stop();
      this.toastr.error(err);
      // alert(err);
    }
    
  }
  onNoClick(): void {
    this.dialogRef.close();
  }




  public doLogin = () => {
    return new Promise((res,rej)=>{
      const email = this.userObj.email;
      // santosh@gmail.com
      const pass = this.userObj.password;
      //123456
      const loginQuery = gql`
      query login($email: String!,$pass:String!) {
        login(password:$pass,email:$email){
          id,
          email,phone,userProfile{id,firstName,lastName,gender,dob},
          userAddress{id,userId,name,pinCode,address,district,state}
        }
      }
    `;
   this.apollo
    .watchQuery({
      query: loginQuery,
      variables: {
        email:email,
        pass:pass
      },
    })
    .valueChanges.subscribe(({data}) => {
     res(data);
    },(err)=>{
      rej(err);
    });
        })
  }

  ngOnDestroy() {
    
  }
}
